:root {
  --pink: #ff6f91;
  --navy: #1e2a78;
  --gradient1: linear-gradient(135deg, #ff6f91, #f45b69);
  --gradient2: linear-gradient(135deg, #6157ff, #1e2a78);
  --panel: #ffffffe6;
  --glasspanel: #ffffff26;
  --text: #1a1a1a;
  --radius: 16px;
  --shadow: 0 12px 32px #00000040;
  --shadow-hover: 0 18px 42px #00000059;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: linear-gradient(135deg, var(--pink), var(--navy));
  min-height: 100vh;
  color: var(--text);
  background-size: 400% 400%;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  font-family: Open Sans, sans-serif;
  animation: 15s infinite gradientBG;
  display: flex;
  overflow-x: hidden;
}

@keyframes gradientBG {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

#bg-canvas {
  z-index: -1;
  pointer-events: none;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.container {
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 750px;
  display: flex;
}

.header {
  text-align: center;
  background: var(--panel);
  -webkit-backdrop-filter: blur(10px);
  border-radius: var(--radius);
  width: 100%;
  box-shadow: var(--shadow);
  border: 1px solid #ffffff4d;
  margin-bottom: 2rem;
  padding: 2rem;
  transition: all .3s;
  position: relative;
  overflow: hidden;
}

.header:before {
  content: "";
  background: var(--gradient1);
  opacity: .5;
  z-index: 0;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  position: absolute;
  top: -30px;
  right: -30px;
}

.header:after {
  content: "";
  background: var(--gradient2);
  opacity: .5;
  z-index: 0;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  position: absolute;
  bottom: -30px;
  left: -30px;
}

.main-title {
  color: var(--navy);
  z-index: 1;
  margin-bottom: .5rem;
  font-family: Lato, sans-serif;
  font-size: 2.5rem;
  font-weight: 800;
  position: relative;
}

.subtitle {
  color: var(--pink);
  z-index: 1;
  margin-bottom: 1.5rem;
  font-size: 1.2rem;
  font-style: italic;
  position: relative;
}

.description {
  z-index: 1;
  position: relative;
}

.description p {
  margin-bottom: .8rem;
  line-height: 1.6;
}

.help-text {
  opacity: .8;
  font-size: .9rem;
  font-style: italic;
}

.progress-container {
  background: var(--panel);
  border-radius: var(--radius);
  width: 100%;
  box-shadow: var(--shadow);
  border: 1px solid #ffffff4d;
  margin-bottom: 1.5rem;
  padding: 1.2rem;
}

.progress-bar {
  background: #0000001a;
  border-radius: 3px;
  height: 6px;
  margin-bottom: .8rem;
  overflow: hidden;
}

.progress-fill {
  background: var(--gradient1);
  border-radius: 3px;
  width: 0%;
  height: 100%;
  transition: width .4s;
}

.progress-text {
  text-align: center;
  color: var(--navy);
  font-size: .9rem;
  font-weight: 600;
}

#sheSpeaksForm {
  width: 100%;
  position: relative;
}

.form-section {
  background: var(--panel);
  -webkit-backdrop-filter: blur(10px);
  border-radius: var(--radius);
  box-shadow: var(--shadow);
  opacity: 0;
  border: 1px solid #ffffff4d;
  margin-bottom: 1.5rem;
  padding: 2rem;
  transition: opacity .5s, transform .5s;
  display: none;
  transform: translateY(20px);
}

.form-section.active {
  opacity: 1;
  display: block;
  transform: translateY(0);
}

.section-header {
  margin-bottom: 2rem;
}

h2 {
  color: var(--navy);
  margin-bottom: .8rem;
  font-family: Lato, sans-serif;
  font-size: 1.8rem;
  font-weight: 800;
  display: inline-block;
  position: relative;
}

h2:after {
  content: "";
  background: var(--gradient1);
  border-radius: 2px;
  width: 60px;
  height: 4px;
  position: absolute;
  bottom: -8px;
  left: 0;
}

.section-subtitle {
  color: #555;
  font-size: 1.1rem;
  font-style: italic;
}

.question-group {
  margin-bottom: 2rem;
}

.question-note {
  color: var(--pink);
  margin-top: .7rem;
  font-size: .9rem;
  font-style: italic;
}

label {
  color: var(--text);
  margin-bottom: .8rem;
  font-size: 1.1rem;
  font-weight: 600;
  display: block;
}

.optional {
  color: #777;
  font-size: .9rem;
  font-style: italic;
  font-weight: normal;
}

.options {
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
  margin-bottom: .5rem;
  display: grid;
}

.options label {
  cursor: pointer;
  background: #fff;
  border: 1px solid #eee;
  border-radius: 12px;
  align-items: center;
  padding: 1rem;
  font-size: .95rem;
  font-weight: 500;
  transition: all .3s;
  display: flex;
  box-shadow: 0 4px 10px #0000000d;
}

.options label:hover {
  border-color: var(--pink);
  transform: translateY(-3px);
  box-shadow: 0 6px 12px #0000001a;
}

.options input {
  accent-color: var(--pink);
  margin-right: .75rem;
  transform: scale(1.2);
}

.checkbox-options {
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
}

input[type="text"], textarea {
  width: 100%;
  color: var(--text);
  background: #fff;
  border: 2px solid #eee;
  border-radius: 12px;
  padding: 1rem;
  font-size: 1rem;
  transition: all .3s;
  box-shadow: inset 0 2px 5px #0000000d;
}

input[type="text"]:focus, textarea:focus {
  border-color: var(--pink);
  outline: none;
  box-shadow: 0 0 0 3px #ff6f9133;
}

textarea {
  resize: vertical;
  min-height: 120px;
}

.advice-input {
  padding: 1.2rem;
  font-size: 1.1rem;
}

.slider-group {
  background: #ffffff80;
  border-radius: 12px;
  margin-bottom: 1rem;
  padding: 1.5rem;
}

.slider-group label {
  color: var(--text);
  justify-content: space-between;
  margin-top: .5rem;
  margin-bottom: .5rem;
  font-family: Lato, sans-serif;
  font-size: .95rem;
  display: flex;
}

.slider-labels {
  color: #777;
  justify-content: space-between;
  margin-top: .3rem;
  font-size: .8rem;
  display: flex;
}

input[type="range"] {
  appearance: none;
  background: #ddd;
  border-radius: 3px;
  width: 100%;
  height: 6px;
  margin: .5rem 0;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  background: var(--gradient1);
  cursor: pointer;
  border: 2px solid #fff;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  box-shadow: 0 2px 6px #0003;
}

input[type="range"]::-moz-range-thumb {
  background: var(--gradient1);
  cursor: pointer;
  border: 2px solid #fff;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  box-shadow: 0 2px 6px #0003;
}

.slider-value {
  color: var(--navy);
  text-align: center;
  min-width: 24px;
  font-family: Lato;
  font-size: 1.1rem;
  font-weight: 600;
}

.nav-buttons {
  justify-content: space-between;
  gap: 1rem;
  margin-top: 2.5rem;
  display: flex;
}

.nav-btn, .submit-nav-btn {
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  padding: .9rem 1.5rem;
  font-family: Lato, sans-serif;
  font-size: 1rem;
  font-weight: 700;
  transition: all .3s;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 12px #00000026;
}

.next-btn {
  background: linear-gradient(45deg, var(--pink), var(--navy));
  background-size: 200% 200%;
  margin-left: auto;
  padding-right: 1.8rem;
}

.prev-btn {
  -webkit-backdrop-filter: blur(5px);
  background: #0003;
  padding-left: 1.8rem;
}

.submit-nav-btn {
  background: linear-gradient(45deg, #ff4081, #ff9100) 0 0 / 200% 200%;
  flex: 1;
}

.arrow {
  font-size: 1.2rem;
  transition: transform .3s;
}

.next-btn:hover .arrow {
  transform: translateX(4px);
}

.prev-btn:hover .arrow {
  transform: translateX(-4px);
}

.nav-btn:before, .submit-nav-btn:before {
  content: "";
  background: linear-gradient(90deg, #0000, #fff3, #0000);
  width: 100%;
  height: 100%;
  transition: all .5s;
  position: absolute;
  top: 0;
  left: -100%;
}

.nav-btn:hover, .submit-nav-btn:hover {
  background-position: 100%;
  transform: translateY(-3px);
  box-shadow: 0 8px 20px #0003;
}

.nav-btn:hover:before, .submit-nav-btn:hover:before {
  left: 100%;
}

.submit-section {
  text-align: center;
  width: 100%;
  margin-top: 2rem;
}

.submit-btn {
  background: linear-gradient(45deg, var(--pink), var(--navy));
  color: #fff;
  cursor: pointer;
  width: 100%;
  max-width: 400px;
  box-shadow: var(--shadow);
  border: none;
  border-radius: 16px;
  flex-direction: column;
  align-items: center;
  margin: 0 auto 1.5rem;
  padding: 1.5rem;
  font-family: Lato, sans-serif;
  font-weight: 700;
  transition: all .3s;
  display: flex;
}

.btn-text {
  margin-bottom: .3rem;
  font-size: 1.2rem;
}

.btn-subtext {
  opacity: .9;
  font-size: .9rem;
}

.submit-btn:hover {
  box-shadow: var(--shadow-hover);
  transform: translateY(-5px);
}

.final-note {
  color: #555;
  margin-top: 1rem;
  font-style: italic;
}

.thank-you {
  background: var(--panel);
  -webkit-backdrop-filter: blur(10px);
  border-radius: var(--radius);
  text-align: center;
  box-shadow: var(--shadow);
  border: 1px solid #ffffff4d;
  width: 100%;
  padding: 3rem 2rem;
  animation: .6s forwards fadeInScale;
  position: relative;
  overflow: hidden;
}

@keyframes fadeInScale {
  from {
    opacity: 0;
    transform: scale(.9);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

.thank-you:before {
  content: "";
  background: var(--gradient1);
  opacity: .5;
  z-index: 0;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  position: absolute;
  top: -50px;
  right: -50px;
}

.thank-you:after {
  content: "";
  background: var(--gradient2);
  opacity: .5;
  z-index: 0;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  position: absolute;
  bottom: -50px;
  left: -50px;
}

.thank-you-content {
  z-index: 1;
  position: relative;
}

.thank-you h2 {
  margin-bottom: 1.5rem;
}

.thank-you p {
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

.sparkles {
  color: var(--pink);
  margin: 1.5rem 0;
  font-size: 2rem;
}

.final-message {
  color: var(--navy);
  font-size: 1.2rem;
  font-weight: 600;
}

@media (width <= 768px) {
  body {
    padding: 1.5rem;
  }

  .main-title {
    font-size: 2rem;
  }

  .subtitle {
    font-size: 1.1rem;
  }

  .header, .form-section, .thank-you {
    padding: 1.8rem;
  }

  h2 {
    font-size: 1.5rem;
  }

  .section-subtitle {
    font-size: 1rem;
  }

  .options {
    grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
  }
}

@media (width <= 480px) {
  body {
    padding: 1rem;
  }

  .main-title {
    font-size: 1.7rem;
  }

  .subtitle {
    font-size: 1rem;
  }

  .header, .form-section, .thank-you {
    padding: 1.5rem;
  }

  h2 {
    font-size: 1.3rem;
  }

  .options {
    grid-template-columns: 1fr;
  }

  .nav-buttons {
    flex-direction: column;
  }

  .prev-btn, .next-btn {
    justify-content: center;
    width: 100%;
    padding: .9rem;
  }

  .prev-btn {
    order: 2;
  }

  .next-btn {
    order: 1;
    margin-bottom: .8rem;
  }

  .submit-nav-btn {
    width: 100%;
    padding: .9rem;
  }
}
